body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#python {
  margin: auto auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 80%;
  height: 80%;
}
.toolbar {
  height: 30px;
  line-height: 30px;
  padding: 0 10px;
}
.editor{
  float: left;
  border-radius: 10px;
  border: 1px solid #949494;
  width: 45%;
  height: 100%;
}
#uploadInput {
  position: absolute;
  visibility: hidden;
}
#editor_1 {
  width: 100%;
  height: 100%;
}
#editor {
  width: 100%;
  height: 100%;
  float: left;
}
.gutter{
  width: 30px;
  height: 400px;
  float: left;
}
.dispaly{
  float: right;
  width: 45%;
  height: 100%;
  background: #f4f4f4;
  opacity: 0.5;
  border-radius: 10px;
  border: 1px solid #949494;
  margin-top: 30px;
}
.dispaly::before{
  content: '\8F93\51FA:';
  display: block;
  height: 30px;
  position: absolute;
  top: 0;
}
#output{
  height: 50%;
  border-bottom: 1px solid #949494;
  white-space: pre-wrap;
  padding: 0 10px;
}
.canvas-wrap {
  width: 400px;
  margin: auto;
  height: calc(50% - 30px);
  position: relative;
}
#axisCanvas {
  width: 400px;
  height: 400px;
  position: absolute;
  display: block;
  margin-top: 0px;
  z-index: 4;
  top: 0;
}
